<template>
    <el-card>
        <div style="margin: 10px;">
            <el-row :gutter="20">
                <el-col :span="24">
                    <el-input placeholder="用户"
                              prefix-icon="el-icon-user"
                              v-model="username"/>
                </el-col>
                <el-col :span="24">
                    <el-input placeholder="新密码"
                              prefix-icon="el-icon-unlock"
                              v-model="password"
                              show-password/>
                </el-col>
                <el-col :span="24">
                    <el-input placeholder="口令"
                              prefix-icon="el-icon-key"
                              v-model="secretCode"/>
                </el-col>
                <el-col :span="12">
                    <el-button style="width: 100%;"
                               @click="resetPassword()">重置密码
                    </el-button>
                </el-col>
                <el-col :span="12">
                    <el-button style="width: 100%;"
                               @click="sendSecretCode()">获取口令
                    </el-button>
                </el-col>
            </el-row>
        </div>
        <div style="margin: 10px;">
            <el-button type="text" @click="routeTo('/entry/login')">返回登录</el-button>
        </div>
    </el-card>
</template>

<script>
    export default {
        name: "EntryForget",
        data() {
            return {
                username: "",
                password: "",
                secretCode: ""
            }
        },
        methods: {
            routeTo: function (path) {
                this.$router.push(path);
            },
            sendSecretCode: function () {
                if (this.username !== '') {
                    this.$http.get('/account/sendSecretCode', {
                        params: {username: this.username}
                    }).then(response => {
                        let js = response.data;
                        if (js.code === 200) {
                            this.$message.success('口令已发送至邮箱');
                        } else {
                            this.$message.error(js.message);
                        }
                    });
                } else {
                    this.$message.error('请输入用户名');
                }
            },
            resetPassword: function () {
                this.$http.put('/account/resetPassword', {
                    params: {
                        username: this.username,
                        secretCode: this.secretCode,
                        newPassword: this.newPassword
                    }
                }).then(response => {
                    let js = response.data;
                    if (js.code === 200) {
                        this.$message.success('重置密码成功');
                    } else {
                        this.$message.error(js.message);
                    }
                });
            }
        }
    }
</script>

<style scoped>
    .el-col {
        padding-top: 10px;
        padding-bottom: 10px;
    }
</style>