<template>
    <el-card>
        <div style="margin: 10px;">
            <el-row :gutter="20">
                <el-col :span="24">
                    <el-input placeholder="用户"
                              prefix-icon="el-icon-user"
                              v-model="username"/>
                </el-col>
                <el-col :span="24">
                    <el-input placeholder="昵称"
                              prefix-icon="el-icon-user"
                              v-model="nickname"/>
                </el-col>
                <el-col :span="24">
                    <el-input placeholder="邮箱"
                              prefix-icon="el-icon-message"
                              v-model="email"/>
                </el-col>
                <el-col :span="24">
                    <el-input placeholder="密码"
                              prefix-icon="el-icon-unlock"
                              v-model="password"
                              show-password/>
                </el-col>
                <el-col :span="12">
                    <el-input placeholder="验证码"
                              prefix-icon="el-icon-key"
                              v-model="puzzleCode"/>
                </el-col>
                <el-col :span="12">
                    <el-image :src="puzzleImageUrl"
                              class="puzzle-image"/>
                </el-col>
                <el-col :span="24">
                    <el-button style="width: 100%;"
                               @click="register()">注册
                    </el-button>
                </el-col>
            </el-row>
        </div>
        <div style="margin: 10px;">
            <el-button type="text" @click="routeTo('/entry/login')">返回登录</el-button>
        </div>
    </el-card>
</template>

<script>
    export default {
        name: "EntryRegister",
        data() {
            return {
                username: "",
                nickname: "",
                password: "",
                email: "",
                puzzleCode: "",
                puzzleImageUrl: ""
            }
        },
        mounted() {
            this.loadPuzzle();
        },
        methods: {
            routeTo: function (path) {
                this.$router.push(path);
            },
            loadPuzzle: function () {
                this.$http.get('/account/puzzle')
                    .then(response => {
                        let js = response.data;
                        if (js.code === 200) {
                            this.puzzleImageUrl = js.data['imageUrl'];
                        } else {
                            this.$message.error(js.message);
                        }
                    });
            },
            register: function () {
                this.$http.post('/account/register', {
                    params: {
                        username: this.username,
                        password: this.password,
                        nickname: this.nickname,
                        email: this.email,
                        puzzleCode: this.puzzleCode
                    }
                }).then(response => {
                    let js = response.data;
                    if (js.code === 200) {
                        this.$message.success('注册成功');
                    } else {
                        this.$message.error(js.message);
                    }
                });
            }
        },

    }
</script>

<style scoped>
    .el-col {
        padding-top: 10px;
        padding-bottom: 10px;
    }

    .puzzle-image {
        height: 40px;
        width: 100%;
    }
</style>