<template>
    <div style="margin: 20px;">
        <el-card shadow="never">
            <el-row :gutter="10">
                <el-col v-for="item in videos"
                        :key="item.ID" :span="3">
                    <div style="margin: 10px; height:100%;">
                        <img :src="item.thumbUrl"
                             :alt="item.title"
                             class="video-thumb">
                        <el-link class="video-title"
                                 :underline="false"
                                 @click="toVideo(item.ID)">
                            {{item.title.substring(0,20)}}
                        </el-link>
                    </div>
                </el-col>
            </el-row>
            <br/>
            <el-row :gutter="10" style="margin: 10px;">
                <el-col :offset="8" :span="8">
                    <el-pagination layout="prev, pager, next"
                                   :current-page.sync="pagination.current"
                                   :page-count="pagination.total"
                                   @current-change="loadVideos()"/>
                </el-col>
                <el-col :offset="6" :span="2">
                    <el-select v-model="order" size="small"
                               placeholder="排序类型"
                               @change="loadVideos()">
                        <el-option v-for="item in orders" :key="item"
                                   :label="item" :value="item"/>
                    </el-select>
                </el-col>
            </el-row>
        </el-card>
    </div>
</template>

<script>
    export default {
        name: "IndexCategory",
        data() {
            return {
                category: this.$route.query.category,
                order: '综合排序',
                orders: ['综合排序'],
                pagination: {
                    current: 1,
                    total: 10
                },
                videos: []
            }
        },
        mounted() {
            this.initOrders();
            this.loadVideos();
        },
        watch: {
            "$route": function () {
                this.category = this.$route.query.category;
                this.loadVideos();
            }
        },
        methods: {
            toVideo: function (videoID) {
                this.$router.push({
                    path: '/video/',
                    query: {ID: videoID}
                });
            },
            initOrders: function () {
                this.$http.get('/video/orders')
                    .then(response => {
                        let js = response.data;
                        if (js.code === 200) {
                            this.orders = js.data.orders;
                        } else {
                            this.$message.error(js.message);
                        }
                    });
            },
            loadVideos: function () {
                this.$http.get('/video/search', {
                    params: {
                        page: this.pagination.current,
                        category: this.category,
                        order: this.order
                    }
                }).then(response => {
                    let js = response.data;
                    if (js.code === 200) {
                        this.videos = js.data.videos;
                        this.pagination.total = js.data.total;
                    } else {
                        this.$message.error(js.message);
                    }
                });
            }
        }
    }
</script>

<style scoped>
    .video-title {
        font-size: small;
        margin-top: 5px;
    }

    .video-thumb {
        width: 100%;
        border-radius: 5px;
        height: 100px;
    }
</style>