<template>
    <el-menu router unique-opened
             class="el-menu-demo"
             mode="horizontal">
        <el-menu-item index="/index/face">
            <template slot="title">
                <i class="el-icon-house"/>
                <span>首页</span>
            </template>
        </el-menu-item>
        <el-menu-item index="/index/search">
            <template slot="title">
                <i class="el-icon-search"/>
                <span>搜索</span>
            </template>
        </el-menu-item>
        <el-submenu index="/index/category">
            <template slot="title">
                <i class="el-icon-video-camera"/>
                <span>分类</span>
            </template>
            <el-menu-item v-for="item in categories" :key="item"
                          :index="'/index/'+item"
                          @click="toCategory(item)">{{item}}
            </el-menu-item>
        </el-submenu>
        <el-submenu index="/index/account">
            <template slot="title">
                <i class="el-icon-user"/>
                <span>用户</span>
            </template>
            <div v-if="loginState">
                <el-menu-item index="/home">个人中心</el-menu-item>
                <el-menu-item @click="logout()">注销</el-menu-item>
            </div>
            <div v-else>
                <el-menu-item index="/entry/login">登录</el-menu-item>
                <el-menu-item index="/entry/register">注册</el-menu-item>
            </div>
        </el-submenu>
    </el-menu>
</template>

<script>
    export default {
        name: "IndexNavmenu",
        data() {
            return {
                loginState: false,
                categories: []
            }
        },
        mounted() {
            this.initCategories();
            this.initAccount();
        },
        methods: {
            initAccount: function () {
                this.$http.get('/account/info')
                    .then(response => {
                        let js = response.data;
                        if (js.code === 200) {
                            this.loginState = true;
                            this.$store.commit('account', js.data);
                        }
                    });
            },
            logout: function () {
                this.$http.get('/account/logout')
                    .then(response => {
                        let js = response.data;
                        if (js.code === 200) {
                            this.$store.commit('account', null);
                            this.loginState = false;
                        } else {
                            this.$message.error(js.message);
                        }
                    });
            },
            initCategories: function () {
                this.$http.get('/video/categories')
                    .then(response => {
                        let js = response.data;
                        if (js.code === 200) {
                            this.categories = js.data.categories;
                        } else {
                            this.$message.error(js.message);
                        }
                    });
            },
            toCategory: function (name) {
                this.$router.push({
                    path: '/index/category',
                    query: {category: name}
                });
            }
        }
    }
</script>

