<template>
    <div>
        <el-page-header :content="video.title"
                        style="margin-bottom: 20px;"
                        @back="goBack()"/>
        <video :src="video.url" style="width: 100%; height: auto;" controls autoplay>
            您的浏览器不支持该视频播放。
        </video>
        <el-row :gutter="20" style="margin-top: 5px; margin-bottom: 5px;">
            <el-col :span="16">
                <el-link :underline="false"
                         icon="el-icon-user"
                         @click="toZone(video.UID)">
                    上传用户：{{video.nickname}}
                </el-link>
            </el-col>
            <el-col :span="8" style="text-align: right;">
                <el-link type="danger"
                         :underline="false"
                         icon="el-icon-thumb"
                         @click="like(video.ID)">
                    点赞（{{video.likeCount}}）
                </el-link>
                <el-link type="warning"
                         :underline="false"
                         icon="el-icon-star-off"
                         @click="collect(video.ID)">
                    收藏（{{video.collectCount}}）
                </el-link>
                <el-link type="info"
                         :underline="false"
                         icon="el-icon-view">
                    播放（{{video.visitCount}}）
                </el-link>
            </el-col>
        </el-row>
        <p class="introduction-text">
            简介：{{video.introduction}}
        </p>
    </div>
</template>

<script>
    export default {
        name: "VideoPlaying",
        props: {
            ID: {type: String, required: true}
        },
        data() {
            return {
                video: {
                    ID: "",
                    title: "",
                    introduction: "",
                    url: "",
                    thumbUrl: "",
                    duration: "",
                    uploadTime: "",
                    UID: 0,
                    nickname: "",
                    avatarUrl: "",
                    visitCount: 0,
                    likeCount: 0,
                    collectCount: 0
                }
            }
        },
        watch: {
            'ID': function () {
                this.loadVideo();
            }
        },
        mounted() {
            this.loadVideo();
        },
        methods: {
            goBack: function () {
                this.$router.go(-1);
            },
            loadVideo: function () {
                this.$http.get('/video/detail',
                    {params: {ID: this.ID}}
                ).then(response => {
                    let js = response.data;
                    if (js.code === 200) {
                        this.video = js.data;
                    } else {
                        this.$message.error(js.message);
                    }
                });
            },
            like: function (ID) {
                this.$http.put('/video/like',
                    {params: {ID: ID}}
                ).then(response => {
                    let js = response.data;
                    if (js.code === 200) {
                        this.$message.success("点赞成功");
                        this.video.likeCount++;
                    } else {
                        this.$message.error(js.message);
                    }
                });
            },
            collect: function (ID) {
                this.$http.post('/video/collect',
                    {params: {ID: ID}}
                ).then(response => {
                    let js = response.data;
                    if (js.code === 200) {
                        this.$message.success("收藏成功");
                        this.video.collectCount++;
                    } else {
                        this.$message.error(js.message);
                    }
                });
            },
            toZone: function (UID) {
                this.$router.push({
                    path: '/zone/',
                    query: {UID: UID}
                });
            },
            toVideo: function (ID) {
                this.$router.push({
                    path: '/video/',
                    query: {ID: ID}
                });
            }
        }
    }
</script>

<style scoped>
    .introduction-text {
        font-size: small;
        color: dimgray;
    }
</style>