<template>
    <el-menu class="el-menu-vertical-demo"
             router unique-opened>
        <div v-if="!isAvatarNew" class="avatar-image">
            <el-avatar :size="100" :src="this.$store.getters.account.avatarUrl"/>
            <input type="file" accept="image/png,image/jpeg"
                   id="avatarInput" style="display: none;"/>
        </div>
        <div class="nickname-text">
            <el-tooltip placement="top" content="更换头像">
                <el-link style="color: gray; font-weight: bold;"
                         :underline="false" @click="setAvatar()">
                    {{this.$store.getters.account.nickname}}
                </el-link>
            </el-tooltip>
        </div>
        <el-menu-item v-for="item in menuItems"
                      :index="item.index"
                      :key="item.index">
            <template slot="title">
                <i :class="item.icon"/>
                <span>{{item.title}}</span>
            </template>
        </el-menu-item>
    </el-menu>
</template>

<script>
    export default {
        name: "HomeNavmenu",
        mounted() {
            this.initAvatarInput();
        },
        data() {
            return {
                avatarInput: null,
                isAvatarNew: false,
                menuItems: [
                    {
                        title: '关注用户',
                        index: '/home/following',
                        icon: 'el-icon-user'
                    }, {
                        title: '我的视频',
                        index: '/home/uploading',
                        icon: 'el-icon-video-play'
                    }, {
                        title: '收藏视频',
                        index: '/home/collecting',
                        icon: 'el-icon-star-off'
                    }, {
                        title: '观看历史',
                        index: '/home/history',
                        icon: 'el-icon-view'
                    }, {
                        title: '评论管理',
                        index: '/home/comment',
                        icon: 'el-icon-chat-dot-round'
                    }, {
                        title: '上传视频',
                        index: '/home/editor',
                        icon: 'el-icon-upload2'
                    }, {
                        title: '用户信息',
                        index: '/home/account',
                        icon: 'el-icon-edit-outline'
                    }
                ]
            }
        },
        methods: {
            initAvatarInput: function () {
                let avatarInput = document.getElementById('avatarInput');
                this.avatarInput = avatarInput;
                avatarInput.onchange = () => {
                    let image = avatarInput.files[0];
                    let data = new FormData();
                    data.append('imageData', image);
                    this.$http.post('/account/setAvatar', data)
                        .then(response => {
                            let js = response.data;
                            if (js.code === 200) {
                                //刷新用户头像
                                this.isAvatarNew = true;
                                this.$store.getters.account.avatarUrl = js['newAvatarUrl'];
                                this.isAvatarNew = false;
                            } else {
                                this.$message.error(js.message);
                            }
                        });
                };
            },
            setAvatar: function () {
                this.avatarInput.click();
            }
        }
    }
</script>

<style scoped>
    .nickname-text,
    .avatar-image {
        text-align: center;
        margin-bottom: 15px;
        margin-top: 15px;
    }
</style>