<template>
    <div>
        <el-row :gutter="10">
            <el-col v-for="item in accounts"
                    :key="item.UID"
                    :span="6">
                <el-card shadow="hover"
                         class="account-card"
                         style="height:100%;">
                    <el-row :gutter="5">
                        <el-col :span="12">
                            <el-avatar :size="80" :src="item.avatarUrl"/>
                        </el-col>
                        <el-col :span="12">
                            <div style="margin-top: 5px;">
                                <el-link :underline="false"
                                         @click="toZone(item.UID)">
                                    <strong class="nickname-link">
                                        {{item.nickname}}
                                    </strong>
                                </el-link>
                            </div>
                            <div class="cancel-follow-link">
                                <el-link type="info" :underline="false"
                                         icon="el-icon-minus"
                                         @click="cancelFollow(item.UID)">取消关注
                                </el-link>
                            </div>
                        </el-col>
                    </el-row>
                </el-card>
            </el-col>
        </el-row>
    </div>
</template>

<script>
    export default {
        name: "HomeFollowing",
        data() {
            return {
                accounts: []
            }
        },
        mounted() {
            this.loadFollowing();
        },
        methods: {
            loadFollowing: function () {
                this.$http.get('/social/followed')
                    .then(response => {
                        let js = response.data;
                        if (js.code === 200) {
                            this.accounts = js.data.accounts;
                        } else {
                            this.$message.error(js.message);
                        }
                    });
            },
            cancelFollow: function (UID) {
                this.$http.delete('/social/cancelFollow', {
                    params: {UID: UID}
                }).then(response => {
                    let js = response.data;
                    if (js.code === 200) {
                        this.accounts = this.accounts.filter(ele => ele.UID !== UID);
                    } else {
                        this.$message.error(js.message);
                    }
                });
            },
            toZone: function (UID) {
                this.$router.push({
                    path: '/zone/',
                    query: {UID: UID}
                });
            }
        }
    }
</script>

<style scoped>
    .account-card {
        margin: 10px;
    }

    .nickname-link {
        font-family: 'Times New Roman', monospace;
    }

    .cancel-follow-link {
        position: absolute;
        bottom: 10px;
    }
</style>