<template>
    <div style="margin-top: 45px;">
        <div style="margin-bottom: 5px;"
             v-for="item in videos" :key="item.ID">
            <el-card shadow="hover"
                     :body-style="{padding: 0}"
                     style="margin: 10px;">
                <el-row :gutter="5">
                    <el-col :span="10">
                        <img :src="item.thumbUrl"
                             :alt="item.title"
                             class="video-thumb">
                    </el-col>
                    <el-col :span="14">
                        <div style="margin: 10px;">
                            <el-link :underline="false"
                                     class="video-title"
                                     @click="toVideo(item.ID)">
                                {{item.title.substring(0,35)}}
                            </el-link>
                        </div>
                    </el-col>
                </el-row>
            </el-card>
        </div>
        <div class="reload-button">
            <el-button type="primary" plain
                       icon="el-icon-refresh-right"
                       style="width: 100%;"
                       @click="loadVideos()">刷新列表
            </el-button>
        </div>
    </div>
</template>

<script>
    export default {
        name: "VideoSimilar",
        props: {
            ID: {type: String, required: true}
        },
        data() {
            return {
                videos: []
            }
        },
        watch: {
            'ID': function () {
                this.loadVideos();
            }
        },
        mounted() {
            this.loadVideos();
        },
        methods: {
            toVideo: function (videoID) {
                this.$router.push({
                    path: '/video/',
                    query: {ID: videoID}
                });
            },
            loadVideos: function () {
                this.$http.get('/video/relative', {
                    params: {videoID: this.ID}
                }).then(response => {
                    let js = response.data;
                    if (js.code === 200) {
                        this.videos = js.data.videos;
                    } else {
                        this.$message.error(js.message);
                    }
                });
            }
        }
    }
</script>

<style scoped>
    .video-title {
        font-size: small;
    }

    .video-thumb {
        width: 100%;
        display: block;
        height: 100px;
    }

    .reload-button {
        margin-left: 10px;
        margin-right: 10px;
    }
</style>