<template>
    <el-card>
        <div slot="header">
            <span>TA的视频</span>
        </div>
        <el-row :gutter="20">
            <el-col :span="4" v-for="item in videos" :key="item.ID">
                <div style="margin: 10px; height:100%;">
                    <img :src="item.thumbUrl"
                         :alt="item.title"
                         class="video-thumb">
                    <el-link :underline="false"
                             class="video-title"
                             @click="toVideo(item.ID)">
                        {{item.title.substring(0,20)}}
                    </el-link>
                </div>
            </el-col>
        </el-row>
        <br/>
        <div class="pagination-bar">
            <el-pagination layout="prev, pager, next"
                           :current-page.sync="pagination.current"
                           :page-count="pagination.total"
                           @current-change="loadVideos()"/>
        </div>
    </el-card>
</template>

<script>
    export default {
        name: "ZoneVideo",
        props: {
            UID: {type: Number, required: true}
        },
        data() {
            return {
                pagination: {
                    total: 1,
                    current: 1,
                },
                videos: []
            }
        },
        mounted() {
            this.loadVideos();
        },
        methods: {
            loadVideos: function () {
                this.$http.get('/video/uploaded', {
                    params: {
                        UID: this.UID,
                        page: this.pagination.current
                    }
                }).then(response => {
                    let js = response.data;
                    if (js.code === 200) {
                        this.pagination.total = js.data.total;
                        this.videos = js.data.videos;
                    } else {
                        this.$message.error(js.message);
                    }
                });
            },
            toVideo: function (ID) {
                this.$router.push({
                    path: '/video/',
                    query: {ID: ID}
                });
            }
        }
    }
</script>

<style scoped>
    .video-thumb {
        width: 100%;
        border-radius: 5px;
        height: 100px;
    }

    .video-title {
        font-size: small;
        margin-top: 5px;
    }

    .pagination-bar {
        text-align: center;
        margin: 20px 0;
    }
</style>