<template>
    <el-card>
        <div slot="header">
            <span>TA的评论</span>
        </div>
        <el-row :gutter="20">
            <el-col :span="24" v-for="item in comments" :key="item.ID">
                <p class="comment-text">{{item.text}}</p>
                <el-row :gutter="20">
                    <el-col :span="20">
                        <el-link :underline="false"
                                 class="comment-info"
                                 @click="toVideo(item.videoID)">
                            视频：{{item.videoTitle.substring(0,50)}}
                        </el-link>
                    </el-col>
                    <el-col :span="4">
                        <span class="comment-info">{{item.postTime}}</span>
                    </el-col>
                </el-row>
                <el-divider/>
            </el-col>
        </el-row>
        <div class="pagination-bar">
            <el-pagination layout="prev, pager, next"
                           :current-page.sync="pagination.current"
                           :page-count="pagination.total"
                           @current-change="loadComments()"/>
        </div>
    </el-card>
</template>

<script>
    export default {
        name: "ZoneComment",
        props: {
            UID: {type: Number, required: true}
        },
        data() {
            return {
                pagination: {
                    total: 1,
                    current: 1,
                },
                comments: []
            }
        },
        mounted() {
            this.loadComments();
        },
        methods: {
            loadComments: function () {
                this.$http.get('/comment/user', {
                    params: {
                        UID: this.UID,
                        page: this.pagination.current
                    }
                }).then(response => {
                    let js = response.data;
                    if (js.code === 200) {
                        this.comments = js.data.comments;
                        this.pagination.total = js.data.total;
                    } else {
                        this.$message.error(js.message);
                    }
                });
            },
            toVideo: function (ID) {
                this.$router.push({
                    path: '/video/',
                    query: {ID: ID}
                });
            }
        }
    }
</script>

<style scoped>
    .comment-info {
        font-size: small;
        color: dimgray;
    }

    .comment-text {
        font-size: small;
    }

    .pagination-bar {
        text-align: center;
        margin: 20px 0;
    }
</style>