<template>
    <el-row :gutter="20">
        <el-col :span="8">
            <el-row :gutter="20">
                <el-col :span="24">
                    <el-input placeholder="昵称"
                              prefix-icon="el-icon-user"
                              v-model="nickname"/>
                </el-col>
                <el-col :span="24">
                    <el-input placeholder="邮箱"
                              prefix-icon="el-icon-message"
                              v-model="email"/>
                </el-col>
                <el-col :span="24">
                    <el-input placeholder="旧密码（可选）"
                              prefix-icon="el-icon-unlock"
                              v-model="oldPassword"
                              show-password/>
                </el-col>
                <el-col :span="24">
                    <el-input placeholder="新密码（可选）"
                              prefix-icon="el-icon-unlock"
                              v-model="newPassword"
                              show-password/>
                </el-col>
                <el-col :span="12">
                    <el-button style="width: 100%;"
                               @click="setAccount()">修改信息
                    </el-button>
                </el-col>
                <el-col :span="12">
                    <el-button style="width: 100%;"
                               @click="setPassword()">修改密码
                    </el-button>
                </el-col>
            </el-row>
        </el-col>
    </el-row>
</template>

<script>
    export default {
        name: "HomeAccount",
        data() {
            return {
                nickname: '',
                email: '',
                oldPassword: '',
                newPassword: ''
            }
        },
        mounted() {
            this.initInfo();
        },
        methods: {
            initInfo: function () {
                let account = this.$store.getters.account;
                this.nickname = account.nickname;
                this.email = account.email;
            },
            setPassword: function () {
                this.$http.put('/account/setPassword', {
                    params: {
                        oldPassword: this.oldPassword,
                        newPassword: this.newPassword
                    }
                }).then(response => {
                    let js = response.data;
                    if (js.code === 200) {
                        this.$message.success("修改密码成功");
                    } else {
                        this.$message.error(js.message);
                    }
                });
            },
            setAccount: function () {
                this.$http.put('/account/update', {
                    params: {
                        username: this.username,
                        nickname: this.nickname,
                        email: this.email
                    }
                }).then(response => {
                    let js = response.data;
                    if (js.code === 200) {
                        this.$message.success("保存信息成功");
                        this.initAccount();
                    } else {
                        this.$message.error(js.message);
                    }
                });
            },
            initAccount: function () {
                this.$http.get('/account/info')
                    .then(response => {
                        let js = response.data;
                        if (js.code === 200) {
                            this.$store.commit('account', js.data);
                        } else {
                            this.$message.error(js.message);
                        }
                    });
            }
        }
    }
</script>

<style scoped>
    .el-col {
        padding-top: 10px;
        padding-bottom: 10px;
    }
</style>