<template>
    <el-row>
        <el-col v-for="item in categories"
                :key="item.title" :span="12">
            <el-card style="margin: 10px; height:100%;">
                <div slot="header">
                    <span>{{item.title}}</span>
                    <el-button class="more-button" type="text"
                               @click="toCategory(item.title)">
                        更多<i class="el-icon-arrow-right"/>
                    </el-button>
                </div>
                <el-row :gutter="20">
                    <el-col v-for="video in item.videos"
                            :key="video.ID" :span="6">
                        <div style="margin: 10px;">
                            <img :src="video.thumbUrl"
                                 :alt="video.title"
                                 class="video-thumb">
                            <el-link class="video-title"
                                     :underline="false"
                                     @click="toVideo(video.ID)">
                                {{video.title.substring(0,20)}}
                            </el-link>
                        </div>
                    </el-col>
                </el-row>
            </el-card>
        </el-col>
    </el-row>
</template>

<script>
    export default {
        name: "IndexCards",
        data() {
            return {
                categories: []
            }
        },
        mounted() {
            this.initCategories();
            this.loadVideos();
        },
        methods: {
            toVideo: function (ID) {
                this.$router.push({
                    path: '/video/',
                    query: {ID: ID}
                });
            },
            initCategories: function () {
                this.$http.get('/video/categories')
                    .then(response => {
                        let js = response.data;
                        if (js.code === 200) {
                            this.categories = [];
                            js.data.categories.forEach(title => {
                                let category = {title: title, videos: []};
                                this.categories.push(category);
                                this.loadVideos(category);
                            });
                        } else {
                            this.$message.error(js.message);
                        }
                    });
            },
            loadVideos: function (category) {
                this.$http.get('/video/search', {
                    params: {page: 1, category: category.title}
                }).then(response => {
                    let js = response.data;
                    if (js.code === 200) {
                        category['videos'] = js.data.videos.slice(0, 8);
                    } else {
                        this.$message.error(js.message);
                    }
                });
            },
            toCategory: function (name) {
                this.$router.push({
                    path: '/index/category',
                    query: {category: name}
                });
            }
        }
    }
</script>

<style scoped>
    .more-button {
        float: right;
        padding: 5px 0;
    }

    .video-thumb {
        width: 100%;
        border-radius: 5px;
        height: 100px;
    }

    .video-title {
        font-size: small;
        margin-top: 5px;
    }
</style>