<template>
    <div>
        <el-row :gutter="10">
            <el-col v-for="item in comments"
                    :key="item.ID"
                    :span="6">
                <el-card shadow="hover"
                         class="comment-card"
                         style="height:100%;">
                    <div style="margin-bottom: 10px;">
                        <span class="comment-text">
                            {{splitText(item.text, 100)}}
                        </span>
                    </div>
                    <div>
                        <el-link type="info" :underline="false"
                                 @click="toVideo(item.videoID)"
                                 class="video-title">
                            {{splitText(item.videoTitle, 10)}}
                        </el-link>
                        <el-link type="danger" :underline="false"
                                 class="delete-link"
                                 @click="deleteComment(item.ID)">删除
                        </el-link>
                    </div>
                </el-card>
            </el-col>
        </el-row>

        <div class="pagination-bar">
            <el-pagination layout="prev, pager, next"
                           :current-page.sync="pagination.current"
                           :page-count="pagination.total"
                           @current-change="loadComments()"/>
        </div>
    </div>
</template>

<script>
    export default {
        name: "HomeComment",
        data() {
            return {
                pagination: {
                    current: 1,
                    total: 1
                },
                comments: []
            }
        },
        mounted() {
            this.loadComments();
        },
        methods: {
            splitText: function (title, length) {
                let sub = title.substring(0, length);
                return title.length <= length ? sub : sub + '...';
            },
            loadComments: function () {
                let UID = this.$store.getters.account.UID;
                this.$http.get('/comment/user', {
                    params: {
                        UID: UID,
                        page: this.pagination.current
                    }
                }).then(response => {
                    let js = response.data;
                    if (js.code === 200) {
                        this.comments = js.data.comments;
                        this.pagination.total = js.data.total;
                    } else {
                        this.$message.error(js.message);
                    }
                });
            },
            deleteComment: function (commentID) {
                this.$http.delete('/comment/delete', {
                    params: {commentID: commentID}
                }).then(response => {
                    let js = response.data;
                    if (js.code === 200) {
                        this.comments = this.comments.filter(
                            item => item.ID !== commentID);
                        this.$message.success("评论删除成功");
                    } else {
                        this.$message.error(js.message);
                    }
                });
            },
            toVideo: function (videoID) {
                this.$router.push({
                    path: '/video/',
                    query: {ID: videoID}
                });
            }
        }
    }
</script>

<style scoped>
    .pagination-bar {
        text-align: center;
        margin: 20px 0;
    }

    .delete-link {
        font-size: small;
        float: right;
        margin-bottom: 15px;
    }

    .comment-card {
        margin: 10px;
        height: 100%;
    }

    .comment-text {
        font-size: small;
    }

    .video-title {
        font-size: small;
        float: left;
        margin-bottom: 15px;
    }
</style>