<template>
    <div>
        <input type="file" accept="image/png,image/jpeg"
               id="thumbInput" style="display: none;"/>
        <input type="file" accept="video/mp4,video/ogg"
               id="videoInput" style="display: none;"/>
        <el-row :gutter="20">
            <el-col :span="24">
                <el-input placeholder="标题"
                          prefix-icon="el-icon-video-camera"
                          v-model="videoTitle"/>
            </el-col>
            <el-col :span="24">
                <el-input placeholder="内容简介"
                          type="textarea"
                          v-model="videoIntroduction"
                          rows="10"/>
            </el-col>
            <el-col :span="12">
                <el-tag type="success" class="file-tag">视频：
                    <span v-if="videoFile!=null">{{videoFile.name}}</span>
                    <span v-else>未选择</span>
                </el-tag>
                <el-tag type="primary" class="file-tag">封面：
                    <span v-if="thumbFile!=null">{{thumbFile.name}}</span>
                    <span v-else>未选择</span>
                </el-tag>
            </el-col>
            <el-col :span="3">
                <el-select v-model="videoCategory" placeholder="视频分类">
                    <el-option v-for="item in categories" :key="item"
                               :label="item" :value="item"/>
                </el-select>
            </el-col>
            <el-col :span="3">
                <el-button style="width: 100%;"
                           @click="thumbInput.click()">选择封面
                </el-button>
            </el-col>
            <el-col :span="3">
                <el-button style="width: 100%;"
                           @click="videoInput.click()">选择视频
                </el-button>
            </el-col>
            <el-col :span="3">
                <el-button style="width: 100%;"
                           @click="uploadVideo()">发布视频
                </el-button>
            </el-col>
        </el-row>
    </div>
</template>

<script>
    export default {
        name: "HomeEditor",
        data() {
            return {
                categories: [],
                videoTitle: '',
                videoIntroduction: '',
                videoCategory: '',

                videoFile: null,
                thumbFile: null,
                videoInput: null,
                thumbInput: null
            }
        },
        mounted() {
            this.initCategories();
            this.initVideoInput();
            this.initThumbInput();
        },
        methods: {
            initVideoInput: function () {
                let videoInput = document.getElementById('videoInput');
                this.videoInput = videoInput;
                videoInput.onchange = () => this.videoFile = videoInput.files[0];
            },
            initThumbInput: function () {
                let thumbInput = document.getElementById('thumbInput');
                this.thumbInput = thumbInput;
                thumbInput.onchange = () => this.thumbFile = thumbInput.files[0];
            },
            initCategories: function () {
                this.$http.get('/video/categories')
                    .then(response => {
                        let js = response.data;
                        if (js.code === 200) {
                            this.categories = js.data.categories;
                        } else {
                            this.$message.error(js.message);
                        }
                    });
            },
            uploadVideo: function () {
                if (this.videoFile == null) {
                    this.$message.warning('请选择视频文件');
                } else if (this.thumbFile == null) {
                    this.$message.warning('请选择视频封面');
                } else if (this.videoTitle === '') {
                    this.$message.warning('请输入视频标题');
                } else if (this.thumbFile === '') {
                    this.$message.warning('请输入视频简介');
                } else if (this.videoCategory === '') {
                    this.$message.warning('请选择视频类型');
                } else {
                    let data = new FormData();
                    data.append('data', this.videoFile);
                    data.append('title', this.videoTitle);
                    data.append('introduction', this.videoIntroduction);
                    data.append('thumb', this.thumbFile);
                    data.append('category', this.videoCategory);
                    this.$http.post('/video/upload', data)
                        .then(response => {
                            let js = response.data;
                            if (js.code === 200) {
                                this.$notify({
                                    title: '上传视频完成',
                                    message: '请在我的视频查看',
                                    type: 'success'
                                });
                            } else {
                                this.$notify({
                                    title: '上传视频失败',
                                    message: js.message,
                                    type: 'error'
                                });
                            }
                        });
                    this.$notify({
                        title: '正在上传视频',
                        message: '请勿离开当前页面',
                        type: 'warning'
                    });
                }
            }
        }
    }
</script>

<style scoped>
    .el-col {
        margin-top: 20px;
    }

    .file-tag {
        margin-right: 20px;
        margin-bottom: 10px;
    }
</style>