<template>
    <div>
        <el-row :gutter="10">
            <el-col :span="22">
                <el-input type="textarea"
                          v-model="commentText"
                          rows="3"/>
            </el-col>
            <el-col :span="2">
                <el-button type="primary"
                           class="comment-button"
                           @click="postComment()">评论
                </el-button>
            </el-col>
        </el-row>
        <el-divider/>

        <div v-for="item in comments"
             :key="item.ID">
            <el-row :gutter="20">
                <el-col :span="2" style="text-align: center;">
                    <el-avatar :size="50" :src="item.avatarUrl"/>
                    <br/>
                    <el-link type="info" :underline="false">
                        <span class="nickname-text">
                            {{item.nickname}}
                        </span>
                    </el-link>
                </el-col>
                <el-col :span="22">
                    <span class="comment-text">
                        {{item.text}}
                    </span>
                    <div class="comment-info">
                        <span>{{item.postTime}}</span>
                    </div>
                </el-col>
            </el-row>
            <el-divider/>
        </div>

        <div class="pagination-bar">
            <el-pagination layout="prev, pager, next" background
                           :current-page.sync="pagination.current"
                           :page-count="pagination.total"
                           @current-change="loadComments()"/>
        </div>
    </div>
</template>

<script>
    export default {
        name: "VideoComment",
        props: {
            ID: {type: String, required: true}
        },
        data() {
            return {
                commentText: "",
                pagination: {
                    current: 1,
                    total: 1,
                },
                comments: []
            }
        },
        watch: {
            'ID': function () {
                this.loadComments();
            }
        },
        mounted() {
            this.loadComments();
        },
        methods: {
            loadComments: function () {
                this.$http.get('/comment/video', {
                    params: {
                        videoID: this.ID,
                        page: this.pagination.current
                    }
                }).then(response => {
                    let js = response.data;
                    if (js.code === 200) {
                        this.comments = js.data.comments;
                        this.total = js.data.total;
                    } else {
                        this.$message.error(js.message);
                    }
                });
            },
            postComment: function () {
                if (this.$store.getters.account == null) {
                    this.$message.warning('请登陆后进行操作');
                } else if (this.commentText.length === 0) {
                    this.$message.warning('评论不能为空');
                } else {
                    this.$http.post('/comment/post', {
                        params: {
                            videoID: this.ID,
                            commentText: this.commentText
                        }
                    }).then(response => {
                        let js = response.data;
                        if (js.code === 200) {
                            this.$message.success('评论成功');
                            this.loadComments();
                            this.commentText = '';
                        } else {
                            this.$message.error(js.message);
                        }
                    });
                }
            }
        }
    }
</script>

<style scoped>
    .comment-button {
        width: 100%;
        height: 75px;
    }

    .pagination-bar {
        text-align: center;
        margin: 20px 0;
    }

    .comment-info {
        position: absolute;
        right: 20px;
        bottom: 0;
        font-size: small;
        color: dimgray;
    }

    .comment-text {
        font-size: small;
    }

    .nickname-text {
        font-size: small;
    }
</style>