<template>
    <div>
        <el-row :gutter="5">
            <el-col v-for="item in videos"
                    :key="item.ID"
                    :span="4">
                <el-card shadow="hover"
                         :body-style="{padding: 0}"
                         style="margin: 10px; height:100%;">
                    <img :src="item.thumbUrl"
                         :alt="item.title"
                         class="video-thumb">
                    <div style="margin: 10px 10px 0;">
                        <el-link class="video-title"
                                 :underline="false"
                                 @click="toVideo(item.ID)">
                            {{splitText(item.title, 20)}}
                        </el-link>
                    </div>
                    <div>
                        <span class="video-duration">
                                时间：{{item.duration}}
                        </span>
                        <el-link class="delete-link"
                                 type="danger" :underline="false"
                                 @click="deleteVideo(item.ID)">删除
                        </el-link>
                    </div>
                </el-card>
            </el-col>
        </el-row>

        <div class="pagination-bar">
            <el-pagination layout="prev, pager, next"
                           :current-page.sync="pagination.current"
                           :page-count="pagination.total"
                           @current-change="loadVideos()"/>
        </div>
    </div>
</template>

<script>
    export default {
        name: "HomeUploading",
        data() {
            return {
                pagination: {
                    current: 1,
                    total: 1
                },
                videos: []
            }
        },
        mounted() {
            this.loadVideos();
        },
        methods: {
            splitText: function (title, length) {
                let sub = title.substring(0, length);
                return title.length <= length ? sub : sub + '...';
            },
            loadVideos: function () {
                this.$http.get('/video/uploaded', {
                    params: {page: this.pagination.current}
                }).then(response => {
                    let js = response.data;
                    if (js.code === 200) {
                        this.videos = js.data.videos;
                        this.pagination.total = js.data.total;
                    } else {
                        this.$message.error(js.message);
                    }
                });
            },
            deleteVideo: function (videoID) {
                this.$http.delete('/video/delete', {
                    params: {videoID: videoID}
                }).then(response => {
                    let js = response.data;
                    if (js.code === 200) {
                        this.$message.success("视频删除成功");
                    } else {
                        this.$message.error(js.message);
                    }
                });
            },
            toVideo: function (videoID) {
                this.$router.push({
                    path: '/video/',
                    query: {ID: videoID}
                });
            }
        }
    }
</script>

<style scoped>
    .delete-link {
        font-size: small;
        float: right;
        margin: 10px;
    }

    .video-title {
        font-size: small;
    }

    .video-duration {
        font-size: small;
        color: darkgray;
        float: left;
        margin: 10px;
    }

    .video-thumb {
        width: 100%;
        display: block;
        height: 100px;
    }

    .pagination-bar {
        text-align: center;
        margin: 20px 0;
    }
</style>