<template>
    <el-card>
        <div style="margin: 10px;">
            <el-row :gutter="20">
                <el-col :span="24">
                    <el-input placeholder="用户"
                              prefix-icon="el-icon-user"
                              v-model="username"/>
                </el-col>
                <el-col :span="24">
                    <el-input placeholder="密码"
                              prefix-icon="el-icon-unlock"
                              v-model="password"
                              show-password/>
                </el-col>
                <el-col :span="12">
                    <el-input placeholder="验证码"
                              prefix-icon="el-icon-key"
                              v-model="puzzleCode"/>
                </el-col>
                <el-col :span="12">
                    <el-image :src="puzzleImageUrl"
                              class="puzzle-image"
                              @click="loadPuzzle()"/>
                </el-col>
                <el-col :span="24">
                    <el-button style="width: 100%;"
                               @click="login()">登录
                    </el-button>
                </el-col>
            </el-row>
        </div>
        <div style="margin: 10px;">
            <el-button type="text" @click="routeTo('/entry/register')">注册用户</el-button>
            <el-button type="text" @click="routeTo('/entry/forget')">重置密码</el-button>
        </div>
    </el-card>
</template>

<script>
    export default {
        name: "EntryLogin",
        data() {
            return {
                username: "",
                password: "",
                puzzleCode: "",
                puzzleImageUrl: ""
            }
        },
        mounted() {
            this.loadPuzzle();
        },
        methods: {
            routeTo: function (path) {
                this.$router.push(path);
            },
            login: function () {
                this.$http.get('/account/login', {
                    params: {
                        username: this.username,
                        password: this.password,
                        puzzleCode: this.puzzleCode
                    }
                }).then(response => {
                    let js = response.data;
                    if (js.code === 200) {
                        this.initAccount();
                        this.$router.push('/index/face');
                        window.location.reload();
                    } else {
                        this.$message.error(js.message);
                    }
                });
            },
            loadPuzzle: function () {
                this.$http.get('/account/puzzle')
                    .then(response => {
                        let js = response.data;
                        if (js.code === 200) {
                            this.puzzleImageUrl = js.data['imageUrl'];
                        } else {
                            this.$message.error(js.message);
                        }
                    });
            },
            initAccount: function () {
                this.$http.get('/account/info')
                    .then(response => {
                        let js = response.data;
                        if (js.code === 200) {
                            this.$store.commit('account', js.data);
                        } else {
                            this.$message.error(js.message);
                        }
                    });
            }
        }
    }
</script>

<style scoped>
    .el-col {
        padding-top: 10px;
        padding-bottom: 10px;
    }

    .puzzle-image {
        height: 40px;
        width: 100%;
    }
</style>