<template>
    <el-card>
        <el-row :gutter="10">
            <el-col :span="10">
                <el-avatar :size="80" :src="account.avatarUrl"/>
            </el-col>
            <el-col :span="14">
                <div style="margin-top: 5px;">
                    <el-link :underline="false">
                        <strong class="nickname-text">{{account.nickname}}</strong>
                    </el-link>
                </div>
                <div style="position: absolute; bottom: 15px;">
                    <el-link type="primary" :underline="false"
                             icon="el-icon-plus"
                             v-if="account.followable"
                             @click="follow()">关注
                    </el-link>
                    <el-link type="info" :underline="false"
                             icon="el-icon-minus"
                             v-else @click="cancelFollow()">取消关注
                    </el-link>
                </div>
            </el-col>
        </el-row>
        <br/>
        <el-row :gutter="10">
            <el-col :offset="2" :span="10">
                <span class="statistic-text">
                    作品数量：{{statistic.videoCount}}
                </span>
            </el-col>
            <el-col :offset="2" :span="10">
                <span class="statistic-text">
                    粉丝数量：{{statistic.fansCount}}
                </span>
            </el-col>
            <el-col :offset="2" :span="10">
                <span class="statistic-text">
                    获赞数量：{{statistic.likeCount}}
                </span>
            </el-col>
            <el-col :offset="2" :span="10">
                <span class="statistic-text">
                    播放数量：{{statistic.visitCount}}
                </span>
            </el-col>
        </el-row>
    </el-card>
</template>

<script>
    export default {
        name: "ZoneAccount",
        props: {
            UID: {type: Number, required: true}
        },
        data() {
            return {
                account: {
                    avatarUrl: 'https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png',
                    nickname: '',
                    followable: true
                },
                statistic: {
                    videoCount: 0,
                    fansCount: 0,
                    visitCount: 0,
                    likeCount: 0
                }
            }
        },
        mounted() {
            this.initAccount();
            this.initFollowable();
            this.initStatistic();
        },
        methods: {
            follow: function () {
                if (this.$store.getters.account != null) {
                    this.$http.post('/social/follow', {
                        params: {UID: this.UID}
                    }).then(response => {
                        let js = response.data;
                        if (js.code === 200) {
                            this.account.followable = false;
                        } else {
                            this.$message.error(js.message);
                        }
                    });
                } else {
                    this.$message.warning('请登陆后进行操作');
                }
            },
            cancelFollow: function () {
                if (this.$store.getters.account != null) {
                    this.$http.delete('/social/cancelFollow', {
                        params: {UID: this.UID}
                    }).then(response => {
                        let js = response.data;
                        if (js.code === 200) {
                            this.account.followable = true;
                        } else {
                            this.$message.error(js.message);
                        }
                    });
                } else {
                    this.$message.warning('请登陆后进行操作');
                }
            },
            initFollowable: function () {
                if (this.$store.getters.account != null) {
                    this.$http.get('/social/followed')
                        .then(response => {
                            let js = response.data;
                            if (js.code === 200) {
                                this.account.followable = js.data.accounts.find(item =>
                                    item.UID === this.UID) === undefined;
                            } else {
                                this.$message.error(js.message);
                            }
                        });
                }
            },
            initStatistic: function () {
                this.$http.get('/account/statistic', {
                    params: {UID: this.UID}
                }).then(response => {
                    let js = response.data;
                    if (js.code === 200) {
                        this.statistic = js.data;
                    } else {
                        this.$message.error(js.message);
                    }
                });
            },
            initAccount: function () {
                this.$http.get('/account/info', {
                    params: {UID: this.UID}
                }).then(response => {
                    let js = response.data;
                    if (js.code === 200) {
                        this.account = js.data;
                    } else {
                        this.$message.error(js.message);
                    }
                });
            }
        }
    }
</script>

<style scoped>
    .statistic-text {
        font-size: small;
        color: dimgray;
    }

    .nickname-text {
        font-size: large;
        font-family: "Times New Roman", monospace;
    }
</style>