import Vue from 'vue'
import App from './App.vue'
import Vuex from 'vuex'
import VueRouter from 'vue-router'
import VueResource from 'vue-resource'
import './plugins/element.js'

import EntryForget from "@/components/EntryForget";
import EntryRegister from "@/components/EntryRegister";
import EntryLogin from "@/components/EntryLogin";
import Entry from "@/components/Entry";
import HomeAccount from "@/components/HomeAccount";
import HomeCollecting from "@/components/HomeCollecting";
import HomeComment from "@/components/HomeComment";
import HomeFollowing from "@/components/HomeFollowing";
import HomeHistory from "@/components/HomeHistory";
import HomeEditor from "@/components/HomeEditor";
import HomeUploading from "@/components/HomeUploading";
import Home from "@/components/Home";
import IndexCategory from "@/components/IndexCategory";
import IndexFace from "@/components/IndexFace";
import IndexSearch from "@/components/IndexSearch";
import Index from "@/components/Index";
import Video from "@/components/Video";
import Zone from "@/components/Zone";

Vue.config.productionTip = false;
Vue.use(VueRouter);
Vue.use(VueResource);
Vue.use(Vuex);

const router = new VueRouter({
    routes: [
        {
            path: '/entry/',
            component: Entry,
            children: [
                {path: 'forget', component: EntryForget},
                {path: 'login', component: EntryLogin},
                {path: 'register', component: EntryRegister}
            ]
        }, {
            path: '/home/',
            component: Home,
            children: [
                {path: 'account', component: HomeAccount},
                {path: 'collecting', component: HomeCollecting},
                {path: 'comment', component: HomeComment},
                {path: 'editor', component: HomeEditor},
                {path: 'following', component: HomeFollowing},
                {path: 'history', component: HomeHistory},
                {path: 'uploading', component: HomeUploading}
            ]
        }, {
            path: '/index/',
            component: Index,
            children: [
                {path: 'category', component: IndexCategory},
                {path: 'face', component: IndexFace},
                {path: 'search', component: IndexSearch}
            ]
        }, {
            path: '/video/',
            component: Video
        }, {
            path: '/zone/',
            component: Zone
        }
    ]
});

const store = new Vuex.Store({
    state: {
        account: null,
    },
    getters: {
        account: state => state.account
    },
    mutations: {
        account: (state, account) => state.account = account
    }
});

new Vue({
    router: router,
    store: store,
    render: h => h(App),
}).$mount('#app');
