<template>
    <el-carousel type="card">
        <el-carousel-item v-for="item in videos" :key="item.ID">
            <el-row :gutter="20" class="carousel-item"
                    :style="'background-image: url('+item.thumbUrl+')'">
                <el-col :offset="12" :span="12">
                    <div class="video-info">
                        <el-link :underline="false" @click="toVideo(item.ID)">
                            <h3 class="video-title">{{item.title}}</h3>
                        </el-link>
                        <p class="video-introduction">{{item.introduction}}</p>
                    </div>
                </el-col>
            </el-row>
        </el-carousel-item>
    </el-carousel>
</template>

<script>
    export default {
        name: "IndexCarousel",
        data() {
            return {
                videos: []
            }
        },
        mounted() {
            this.loadCarousel();
        },
        methods: {
            loadCarousel: function () {
                this.$http.get('/video/carousel')
                    .then(response => {
                        let js = response.data;
                        if (js.code === 200) {
                            this.videos = js.data.videos;
                        } else {
                            this.$message.error(js.message);
                        }
                    });
            },
            toVideo: function (videoID) {
                this.$router.push({
                    path: '/video/',
                    query: {ID: videoID}
                });
            }
        }
    }
</script>

<style scoped>
    .video-info {
        position: absolute;
        margin: 15px;
        bottom: 15px;
    }

    .video-title {
        color: ghostwhite;
    }

    .video-introduction {
        font-size: small;
        color: ghostwhite;
    }

    .carousel-item {
        background-color: black;
        height: 100%;
        width: 100%;
        background-size: 100%;
        background-repeat: no-repeat;
    }
</style>